@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
/* ------------ import google fonts ------------*/
/* @import url('https://fonts.googleapis.com/css2?family=Reggae+One&display=swap'); */


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    /* font-family: 'Reggae One', cursive; */
}
a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}
html,body{
    height: 100%;
    scroll-behavior: smooth;
}
  

/* --------- content styling ---------------- */
section{
    padding: 100px 0;
}
.max-width{
    max-width: 1300px;
    padding: 0 80px;
    margin: auto;
}
.about, .services, .skills, .projects, .contact, footer{
    font-family: 'Poppins', sans-serif;
}
.about .about-content, 
.services .serv-content,
.skills .skills-content,
.contact .contact-content{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    min-height: 100%;
}
section .title{
    position: relative;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 60px;
    padding-bottom: 20px;
    font-family: 'Ubuntu', sans-serif;
}
section .title::before{
    content: "";
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 180px;
    height: 3px;
    background: #111;
    transform: translateX(-50%);
}
section .title::after{
    position: absolute;
    bottom: -8px;
    left: 50%;
    font-size: 20px;
    color: #10ecda;
    padding: 0 5px;
    background: #111;
    transform: translateX(-50%);
}

/* ------------navbar styling-------------- */
.navbar{
    position: fixed;
    width: 100%;
    z-index: 999;
    padding: 30px 0;
    font-family: 'Ubuntu', sans-serif;
    transition: all 0.3s ease;
}
.navbar.sticky{
    padding: 15px 0;
    background: #b7c7b317;
}
.navbar.sticky.solid{
    padding: 15px 0;
    background: black;
}
.navbar .max-width{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.navbar .logo a{
    color: #fff;
    font-size: 35px;
    font-weight: 600;
}
.navbar .logo a span{
    color: #10ecda;
    transition: all 0.3s ease;
}
.navbar.sticky .logo a span{
    color: #10ecda;
}
.navbar .menu li{
    list-style: none;
    display: inline-block;
}
.navbar .menu li a{
    display: block;
    color: #fff;
    font-size: 25px;
    font-weight: 500;
    margin-left: 25px;
    font-family: 'Akaya Telivigala', cursive;
    transition: color 0.3s ease;
}
.navbar .menu li a:hover{
    color: #10ecda;
}
.navbar.sticky .menu li a:hover{
    color: #10ecda;
    transform: scale(1.3);
}

.navbar.sticky .menu li a.active {
    color: #10ecda; /* Change color when active */
    transform: scale(1.4); /* Scale effect when active */
}

/*----------- menu btn styling ----------*/
.active {
    color: #10ecda;
}
.menu-btn{
    color: #fff;
    font-size: 23px;
    cursor: pointer;
    display: none;
}
.scroll-up-btn{
    position: fixed;
    height: 45px;
    width: 42px;
    background: #10ecda;
    right: 30px;
    bottom: 10px;
    text-align: center;
    line-height: 45px;
    color: #fff;
    z-index: 9999;
    font-size: 30px;
    border-radius: 6px;
    border-bottom-width: 2px;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s ease;
}
.scroll-up-btn.show{
    bottom: 30px;
    opacity: 1;
    pointer-events: auto;
}
.scroll-up-btn:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

/* --------------side menu styling------------- */

 .side-menu {
    color: #fff;
    background-color: #111;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    display: table;
}
 .side-menu ul {
    display: flex;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    list-style: none;
}  

.side-menu ul li {
    font-size: 80px;
    font-family: 'Akaya Telivigala', cursive;
}

.side-menu ul li:hover {
    color: #10ecda;
    transform: scale(1);
}

.side-menu ul li.active {
    color: red;  /* The color when the link is active */
    transform: scale(1.2);  /* You may want to tone down the scale effect */
}

/* ------------------home section styling----------------------- */
.home{
    display: flex;
    background: url(/portfolio/static/media/HackerBg.15f1892a.jpg) no-repeat right;
    height: 90vh;
    color: #fff;
    min-height: 500px;
    background-size: cover;
    background-attachment: fixed;
    font-family: 'Ubuntu', sans-serif;
}
.home .max-width{
    margin: auto 0 auto 30px;
}
.home .home-content .text-1{
    font-size: 27px;
    font-weight: 100;
}
.home .home-content .text-2{
    font-size: 75px;
    font-weight: 600;
    margin-left: -3px;
}
.home .home-content .text-3{
    font-size: 40px;
    margin: 5px 0;
}
.home .home-content .text-3 span{
    color: #10ecda;
    font-weight: 500;
}
.home .home-content a{
    display: inline-block;
    background: rgba(76, 82, 87, 0.37);
    color: rgb(255, 255, 255);
    font-size: 25px;
    padding: 12px 36px;
    margin-top: 20px;
    font-weight: 400;
    border-radius: 6px;
    border: 2px solid #10ecda;
    transition: all 0.3s ease;
}
.home .home-content a:hover{
    color: #10ecda;
    background: none;
}

/* -------------about section styling---------------- */
.about .title::before{
    background: #fff;
}
.about .title::after{
    content: "who i am";
    background-color: #111;
}
.about {
    background-color: #111;
}
.about h2{
    color:#fff;
}
.about .about-content .left{
    width: 45%;
    /* margin-bottom: 80%; */
}
.about .about-content .left img{
    height: 450px;
    width: 400px;
    object-fit: cover;
    border-radius: 6px;
}
.about .about-content .right{
    width: 55%;
    /* margin-bottom: 80%; */
}
.about .about-content .right .text{
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #fff;
}
.about .about-content .right .text span{
    color: #10ecda;
}
.about .about-content .right p{
    color: #fff;
    text-align: justify;
}
.about .about-content .right a{
    display: inline-block;
    background: rgba(76, 82, 87, 0.37);
    color: #fff;
    font-size: 20px;
    font-weight: 500;
    padding: 10px 30px;
    margin-top: 20px;
    border-radius: 6px;
    border: 2px solid #10ecda;
    transition: all 0.3s ease;
}
.about .about-content .right a:hover{
    color: #10ecda;
    background: none;
}

/* -------------skills section styling------------- */
.skills {
    background-color: #111;
}
.skills .title {
    color: #fff;
}

.skills .title::before{
    background: #fff;
}
.skills .title::after{
    content: "what i know";
}
.skills .skills-content .column{
    width: calc(50% - 100px);
}
.skills .skills-content .left .text{
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #fff;
}
.skills .skills-content .left .text span{
    color: #10ecda;
}
.skills .skills-content .left p{
    text-align: justify;
    color: #fff;
}
.skills .skills-content .left a{
    display: inline-block;
    background: rgba(76, 82, 87, 0.37);
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    padding: 8px 16px;
    margin-top: 20px;
    border-radius: 6px;
    border: 2px solid #10ecda;
    transition: all 0.3s ease;
}
.skills .skills-content .left a:hover{
    color: #10ecda;
    background: none;
}

.skills .skills-content .right{
    margin-bottom: 55%;
}

.skills .skills-content .right .bars{
    margin-bottom: 15px;
    
}
.skills .skills-content .right .bars:hover{
    margin-bottom: 15px;
    transform: scale(1.01);
    background-color: #222;
    /* transform: rotateY(180deg); */
}
.skills .skills-content .right .info{
    display: flex;
    margin-bottom: 5px;
    align-items: center;
    justify-content: space-between;
}
.skills .skills-content .right span{
    font-weight: 500;
    font-size: 18px;
    color: #fff;
}
.skills .skills-content .right .line{
    height: 5px;
    width: 100%;
    background: lightgrey;
    position: relative;
}
.skills .skills-content .right .line::before{
    content: "";
    position: absolute;
    height: 100%;
    left: 0;
    top: 0;
    background: #10ecda;
}
.skills-content .right .cpp::before{
    width: 80%;
}
.skills-content .right .py::before{
    width: 90%;
}
.skills-content .right .java::before{
    width: 80%;
}
.skills-content .right .js::before{
    width: 70%;
}
.skills-content .right .az::before{
    width: 80%;
}
.skills-content .right .sql::before{
    width: 80%;
}
.skills-content .right .fd::before{
    width: 80%;
}
.skills-content .right .uc::before{
    width: 60%;
}
.skills-content .right .lx::before{
    width: 80%;
}
.skills-content .right .re::before{
    width: 70%;
}

/* --------------projects section styling-------------- */

.projects{
    color:#fff;
    background: #111;
}

.projects .title::before{
    background: #fff;
}

.projects .title::after{
    background: #111;
    content: "what i provide";
}

.projects .title::after{
    content: "what i did";
}
.projects .carousel .card{
    background: #222;
    border-radius: 6px;
    padding: 25px 35px;
    text-align: center;
    overflow: hidden;
    transition: all 0.3s ease;
}
.projects .carousel .card:hover{
    background: #0fe4d2b9;
}
.projects .carousel .card .box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
}
.projects .carousel .card:hover .box{
    transform: scale(1.05);
}
.projects .carousel .card .text{
    font-size: 25px;
    font-weight: 500;
    margin: 10px 0 7px 0;
    color: #fff;
}

.projects .carousel .card img{
    height: 150px;
    width: 150px;
    object-fit: cover;
    border-radius: 50%;
    border: 5px solid #10ecda;
    transition: all 0.3s ease;
}
.projects .carousel .card:hover img{
    height: 200px;
    width: 200px;
    border-radius: 25%;
    object-fit: contain;
    border-color: #fff;
}
.owl-dots{
    text-align: center;
    margin-top: 20px;
}
.owl-dot{
    height: 13px;
    width: 13px;
    margin: 0 5px;
    outline: none!important;
    border-radius: 50%;
    border: 2px solid #10ecda!important;
    transition: all 0.3s ease;
}
.owl-dot.active{
    width: 35px;
    border-radius: 14px;
}
.owl-dot.active,
.owl-dot:hover{
    background: #10ecda!important;
}

/* ------------contact section styling------------ */
.contact {
    background-color: #111;
}
.contact .title{
    color: #fff;
}
.contact .title::before{
    background: #fff;
}
.contact .title::after{
    content: "get in touch";
}
.contact .contact-content .column{
    width: calc(50% - 100px);
}
.contact .contact-content .text{
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #fff;
}
.contact .contact-content .text span{
    color: #10ecda;
}
.contact .contact-content .left p{
    text-align: justify;
    color: #fff;
}
.contact .contact-content .left .icons{
    margin: 10px 0;
}
.contact .contact-content .row{
    display: flex;
    height: 65px;
    align-items: center;
}
.contact .contact-content .row .info{
    margin-left: 30px;
    color: #fff;
}
.contact .contact-content .row i{
    font-size: 25px;
    color: #10ecda;
}
.contact .contact-content .info .head{
    font-weight: 500;
}
.contact .contact-content .info .sub-title{
    color: #10ecda;
}
.contact .right form .fields{
    display: flex;
}
.contact .right form .field,
.contact .right form .fields .field{
    height: 45px;
    width: 100%;
    margin-bottom: 15px;
}
.contact .right form .textarea{
    height: 80px;
    width: 100%;
}
.contact .right form .name{
    margin-right: 10px;
}
.contact .right form .email{
    margin-left: 10px;  
}
.contact .right form .field input,
.contact .right form .textarea textarea{
    height: 100%;
    width: 100%;
    border: 1px solid lightgrey;
    border-radius: 6px;
    outline: none;
    padding: 0 15px;
    font-size: 17px;
    font-family: 'Poppins', sans-serif;
    transition: all 0.3s ease;
}
.contact .right form .field input:focus,
.contact .right form .textarea textarea:focus{
    border-color: #b3b3b3;
}
.contact .right form .textarea textarea{
    padding-top: 10px;
    resize: none;
}
.contact .right form .button{
    height: 47px;
    width: 170px;
}
.contact .right form .button button{
    width: 100%;
    height: 100%;
    border: 2px solid #10ecda;
    background: rgba(76, 82, 87, 0.37);
    color: #fff;
    outline: none;
    font-size: 20px;
    font-weight: 500;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.contact .right form .button button:hover{
    color: #10ecda;
    background: none;
}

/* -----------footer section styling-------------- */
footer{
    margin-top: auto;
    display:flex;
    background: black;
    padding: 15px 23px;
    color: #fff;
    text-align: center;
    justify-content: space-around;
    font-family: 'Ubuntu', sans-serif;
    max-height: 95px;
    position: fixed;  /* Fixed position */
    bottom: 0;        /* Stick to the bottom */
    width: 100%;      /* Full width */
    z-index: 1000;    /* Ensure it's on top of other content */
}

footer span{
    color: #10ecda;
    text-decoration: none;
    font-weight: 500;
    font-size: 20px;
}
/* footer span a:hover{
    text-decoration: underline;
}  */
footer .footer-bar .icon li {
    /* display: flex; */
    list-style: none;
    display: inline-block;
    text-align:justify;
    padding: 5px;
}

footer .footer-bar .icon li .f-button {
    color:#fff;
    font-size: 30px;
}
footer .footer-bar .icon li .f-button:hover {
    color:#10ecda;
}

/* -------------responsive media query---------- */
@media (max-width: 1300px) {
    .home .max-width{
        margin-left: 0px;
    }
}

@media (max-width: 1104px) {
    .about .about-content .left img{
        height: 350px;
        width: 350px;
    }
}

@media (max-width: 991px) {
    .max-width{
        padding: 0 50px;
    }
}
@media (max-width: 947px){
    .menu-btn{
        display: block;
        z-index: 999;
    }
    .menu-btn i.active:before{
        content: "\f00d";
    }
    .navbar .menu{
        position: fixed;
        height: 100vh;
        width: 100%;
        left: -100%;
        top: 0;
        background: #111;
        text-align: center;
        padding-top: 80px;
        transition: all 0.3s ease;
    }
    .navbar .menu.active{
        left: 0;
    }
    .navbar .menu li{
        display: block;
    }
    .navbar .menu li a{
        display: inline-block;
        margin: 20px 0;
        font-size: 25px;
    }
    .home .home-content .text-2{
        font-size: 70px;
    }
    .home .home-content .text-3{
        font-size: 35px;
    }
    .home .home-content a{
        font-size: 23px;
        padding: 10px 30px;
    }
    .max-width{
        max-width: 930px;
    }
    .about .about-content .column{
        width: 100%;
    }
    .about .about-content .left{
        display: flex;
        justify-content: center;
        margin: 0 auto 60px;
    }
    .about .about-content .right{
        flex: 100% 1;
    }
    .services .serv-content .card{
        width: calc(50% - 10px);
        margin-bottom: 20px;
    }
    .skills .skills-content .column,
    .contact .contact-content .column{
        width: 100%;
        margin-bottom: 35px;
    }
}

@media (max-width: 690px) {
    .max-width{
        padding: 0 23px;
    }
    .home .home-content .text-2{
        font-size: 60px;
    }
    .home .home-content .text-3{
        font-size: 32px;
    }
    .home .home-content a{
        font-size: 20px;
    }
    .services .serv-content .card{
        width: 100%;
    }
}

@media (max-width: 500px) {
    .home .home-content .text-2{
        font-size: 50px;
    }
    .home .home-content .text-3{
        font-size: 27px;
    }
    .about .about-content .right .text,
    .skills .skills-content .left .text{
        font-size: 19px;
    }
    .contact .right form .fields{
        flex-direction: column;
    }
    .contact .right form .name,
    .contact .right form .email{
        margin: 0;
    }
    .scroll-up-btn{
        right: 15px;
        bottom: 15px;
        height: 38px;
        width: 35px;
        font-size: 23px;
        line-height: 38px;
    }
}

